import React, { Component } from 'react';
import LayerMenu from './LayerMenu';
import ComponentMenu from './ComponentMenu';
import './index.less';
class Page extends Component {
    state = {
        visible: true,
        data: [{
            title: '0',
        }, {
            title: '1',
            // children: [{
            //     title: '线图11'
            // }, {
            //     title: '线图22'
            // }, {
            //     title: '线图33'
            // }]
        }, {
            title: '2'
        }, {
            title: '3'
        }, {
            title: '4'
        }, {
            title: '5'
        }, {
            title: '6'
        }, {
            title: '7'
        }, {
            title: '8'
        }, {
            title: '9'
        }, {
            title: '10'
        }, {
            title: '11'
        }, {
            title: '12'
        }]
    };
    constructor(props) {
        super(props);
    }
    componentDidMount() {
    }
    render() {
        return (
            <div className="test">
                <div className="test-col">
                    <LayerMenu
                        data={ this.state.data }
                    />
                </div>
                <div className="test-col">2</div>
                <div className="test-col">
                    {/* <ComponentMenu 

                    /> */}
                    <p>1. 支持多选：ctrl+选择  </p>
                    <p>2. 支持区间多选：选择 + shift+选择  </p>
                    <p>3. 支持拖动排序  </p>
                </div>
            </div>
        );
    }
}
export default Page;



