import React, { Component } from 'react';
import PropTypes from "prop-types";
import './LayerMenu.less';
import layerImg from '../../assets/images/layer.png';
export default class LayerMenu extends Component {
    static propTypes = {
        sprefix: PropTypes.string
    };
    static defaultProps = {
        sprefix: 'dw'
    };
    constructor(props) {
        super(props);
        let data = props.defaultData;
        this.hotKey = null;
        if ('data' in props) {
            data = props.data || [];
        }
        let activeKey = props.defaultActiveKey || [];
        if ('activeKey' in props) {
            activeKey = props.activeKey || [];
        }
        this.state = {
            activeKey,
            data
        };
    }
    componentWillReceiveProps(nextProps) {
        const newState = {};
        if('data' in nextProps) {
            newState.data = nextProps.data || [];
            
        }
        if('activeKey' in nextProps) {
            newState.activeKey = nextProps.activeKey || [];
        }
        this.setState(newState);
    }
    componentDidMount() {
        document.addEventListener("keydown", this.onHandleKeyDown);
        document.addEventListener("keyup", this.onHandleKeyUp);
    }
    componentWillUnmount(){
        document.removeEventListener("keydown", this.onHandleKeyDown);
        document.removeEventListener("keyup", this.onHandleKeyUp);
    }
    renderNav(item) {
        const { sprefix } = this.props;
        let chk = null;
        if(item.children && item.children.length) {
            chk = (
                <React.Fragment>
                    <input className={ `${ sprefix }-layerMenu-menu-submenu-title-chk` }
                        type="checkbox"
                        // checked={ item.open }
                        // onChange={ (e, param) => {
                        //     // item.open = !item.open;
                        //     // this.setState({
                        //     //     config: {
                        //     //         ...this.state.config
                        //     //     }
                        //     // })
                        // } }
                    />
                    <i className={ `${ sprefix }-layerMenu-menu-switch-icon` }>
                        <svg viewBox="64 64 896 896" focusable="false" data-icon="down" width="1em" height="1em" fill="currentColor" aria-hidden="true">
                            <path d="M884 256h-75c-5.1 0-9.9 2.5-12.9 6.6L512 654.2 227.9 262.6c-3-4.1-7.8-6.6-12.9-6.6h-75c-6.5 0-10.3 7.4-6.5 12.7l352.6 486.1c12.8 17.6 39 17.6 51.7 0l352.6-486.1c3.9-5.3.1-12.7-6.4-12.7z"></path>
                        </svg>
                    </i>
                </React.Fragment>
            );
        }
        return chk;
    }
    renderChildren(item, data) {
        const { sprefix } = this.props;
        let rs;
        if(item.children && item.children.length) {
            rs = (
                <ul className={ `${ sprefix }-layerMenu-menu-sub` }>
                    { this.renderPanel(item.children, data) }
                </ul>
            );
        }
        return rs;
    }
    renderPanel(children = [], data) {
        const { sprefix } = this.props;
        const { activeKey } = this.state;
        const rs = [];
        let i = 0;
        for(let n in children) {
            let titleCls = [`${ sprefix }-layerMenu-menu-submenu-title`];
            if(children[n].children && children[n].children.length) {
                if(data[children[n].name] === undefined) {
                    data[children[n].name] = {};
                }
                titleCls.push(`${ sprefix }-layerMenu-menu-nav`);
            }
            if(activeKey.includes(i)) {
                titleCls.push(`${ sprefix }-layerMenu-menu-submenu-active`);
            }
            rs.push(
                <li key={ i } className={ `${ sprefix }-layerMenu-menu-submenu` }>
                    <div className={ titleCls.join(' ') } style={{ paddingLeft: '0px' }}
                        onContextMenu={ this.handleContextMenu(i) }
                        onClick={ this.handleClick(i) }
                        onDragStart={ this.handleDragStart(i) }
                        onDragOver ={ this.handleDragOver(i) }
                        onDrop={ this.handleDrop }
                        draggable            
                    >
                        <div className={ `${ sprefix }-layerMenu-menu-submenu-title-inner` }>
                            <img className={ `${ sprefix }-layerMenu-thumbail` } src={ layerImg } alt=""/>
                            <span className={ `${ sprefix }-layerMenu-menu-submenu-label` }>
                                { children[n].title === undefined ? null : children[n].title }
                            </span>
                            <div className={ `${ sprefix }-layerMenu-menu-submenu-content` }>
                                {/* { this.renderUIComponent(children[n], data) } */}
                            </div>
                            <i className={ `${ sprefix }-layerMenu-icon-move` }>
                                <svg viewBox="64 64 896 896" focusable="false" data-icon="more" width="1em" height="1em" fill="currentColor" aria-hidden="true">
                                    <path d="M456 231a56 56 0 10112 0 56 56 0 10-112 0zm0 280a56 56 0 10112 0 56 56 0 10-112 0zm0 280a56 56 0 10112 0 56 56 0 10-112 0z"></path>
                                </svg>
                            </i>
                        </div>
                    </div>
                    { this.renderNav(children[n]) }
                    { this.renderChildren(children[n], data[children[n].name]) }
                </li>
            );
            i++;
        }
        return rs;
    }
    setActive(index) {
        const { activeKey } = this.state;
            let rs = activeKey;
            if(this.hotKey === 'ctrl') {
                const n = rs.indexOf(index);
                if(n > -1) {
                    rs.splice(n, 1);
                } else {
                    rs.push(index);
                }
            } else if(this.hotKey === 'shift') {
                let min = 0, max = 0;
                if(activeKey.length > 0) {
                    min = Math.min(...activeKey);
                    max = Math.max(...activeKey);
                }
                rs = Array.from(this.state.data.keys());
                if(min < index && index < max) {
                    rs = rs.slice(min, index + 1);        
                } else if(index < min) {
                    rs = rs.slice(index, min + 1);
                } else if(index > max) {
                    rs = rs.slice(max, index + 1);
                } else {
                    if(activeKey.length === 1 && activeKey[0] == index) {
                        rs = [];
                    } else {
                        rs = [index];
                    }
                }
            } else {
                if(activeKey.length === 1 && activeKey[0] == index) {
                    rs = [];
                } else {
                    rs = [index];
                }
            }
        return rs;    
    }
    handleContextMenu = index => {
        return e => {
            e.preventDefault();
            const { data } = this.state;
            const activeKey = this.setActive(index);
            this.setState({
                activeKey
            });
            this.onChange('contextMenu', { ...this.props, activeKey, data });
        }
    }
    handleClick = index => {
        return e => {
            e.preventDefault();
            const { data } = this.state;
            const activeKey = this.setActive(index);
            this.setState({
                activeKey
            });
            this.onChange('click', { ...this.props, activeKey, data });
        }
    }
    handleDragStart = index => {
        return e => {
            //e.preventDefault();
            this.startIndex = index;
            this.endIndex = index;
        }
    }
    handleDragOver = index => {
        return e => {
            e.preventDefault();
            this.endIndex = index;
        }
    }
    handleDrop = e => {
        e.preventDefault();
        const { activeKey, data } = this.state;
        let newActiveKey;
        if(!activeKey.includes(this.startIndex)) {
            if(this.endIndex > this.startIndex) {
                data.splice(this.endIndex + 1, 0, data[this.startIndex]);
                data.splice(this.startIndex, 1);
                newActiveKey = [this.endIndex];
            } else {
                data.splice(this.endIndex, 0, data[this.startIndex]);
                data.splice(this.startIndex + 1, 1);
                newActiveKey = [this.endIndex];
            }
            const newState = {
                data
            }
            if(newActiveKey) {
                newState.activeKey = newActiveKey;
            }
            this.setState(newState);
        } else {
            const newState = this.moveTo(this.endIndex, activeKey, data);
            // const newState = {
            //     data: newData
            // }
            // if(newActiveKey) {
            //     newState.activeKey = newActiveKey;
            // }
            this.setState(newState);
        }
    }
    moveTo(index, activeKey, data) {
        const min = Math.min(...activeKey);
        let newActiveKey = activeKey;
        newActiveKey.sort((a, b) => a - b);
        const rs = newActiveKey.map(n => data[n]);
        let count = 0, newData = data.filter((item, n) => !newActiveKey.includes(n));
        newActiveKey.forEach(n => {
            if(n <= index) {
                count++;
            }
        });
        if(index > min) {
            newData.splice(index + 1 - count, 0, ...rs);
            newActiveKey = [index + 1 - count];
            for(let i = 1; i < activeKey.length; i++) {
                newActiveKey.push(newActiveKey[0] + i);
            }
        } else {
            if(index === min) {
                count -= 1;
            }
            newData.splice(index - count, 0, ...rs);
            newActiveKey = [index - count];
            for(let i = 1; i < activeKey.length; i++) {
                newActiveKey.push(newActiveKey[0] + i);
            }
        }
        return {
            activeKey: newActiveKey,
            data: newData
        }
    }
    prev() {
        const { activeKey, data } = this.state;
        if(Math.min(...activeKey) <= 0) {
            return;
        }
        let keys = [...activeKey], rs = {
            data
        };
        activeKey.forEach((item, i) => {
            rs = this.moveTo(activeKey[i] - 1, [activeKey[i]], rs.data);
            keys[i] = rs.activeKey[0];
        });
        rs.activeKey = keys;
        return rs;
    }
    next() {
        const { activeKey, data } = this.state;
        if(Math.max(...activeKey) >= data.length - 1) {
            return;
        }
        let keys = [], rs = {
            data
        };
        activeKey.forEach((item, i) => {
            rs = this.moveTo(activeKey[activeKey.length - 1 - i] + 1, [activeKey[activeKey.length - 1 - i]], rs.data);
            keys[activeKey.length - 1 - i] = rs.activeKey[0];
        });
        rs.activeKey = keys;
        return rs;
    }
    handleBtnClick = type => {
        return e => {
            e.preventDefault();
            const { activeKey, data } = this.state;
            if(!activeKey.length) {
                return;
            }
            let newState;
            if(type === 'first') {
                newState = this.moveTo(0, activeKey, data);
            } else if(type === 'add') {
                newState = this.prev();
            } else if(type === 'reduce') {
                newState = this.next();
            } else if(type === 'last') {
                newState = this.moveTo(data.length - 1, activeKey, data);
            }
            if(newState) {
                this.setState(newState);
                this.onChange('drop', { ...this.props, activeKey, data });
            }
        }
    }
    onHandleKeyDown = e => {
        //e.preventDefault();
        //const { activeKey } = this.state;
        if(e.ctrlKey) {
            this.hotKey = 'ctrl';
        } else if(e.shiftKey) {
            this.hotKey = 'shift';
        }
    }
    onHandleKeyUp = e => {
        //e.preventDefault();
        this.hotKey = null;
    }
    onChange(type, param) {
        if(this.props.onChange) {
            this.props.onChange(type, param);
        }
    }
    render() {
        const { sprefix } = this.props;
        const { data } = this.state;
        return (
            <div className={ `${ sprefix }-layerMenu` }>
                <div className={ `${ sprefix }-layerMenu-header` }>
                    <i className={ `${ sprefix }-layerMenu-icon` } onClick={ this.handleBtnClick('first') }>
                        <svg viewBox="64 64 896 896" focusable="false" data-icon="vertical-align-top" width="1em" height="1em" fill="currentColor" aria-hidden="true">
                            <path d="M859.9 168H164.1c-4.5 0-8.1 3.6-8.1 8v60c0 4.4 3.6 8 8.1 8h695.8c4.5 0 8.1-3.6 8.1-8v-60c0-4.4-3.6-8-8.1-8zM518.3 355a8 8 0 00-12.6 0l-112 141.7a7.98 7.98 0 006.3 12.9h73.9V848c0 4.4 3.6 8 8 8h60c4.4 0 8-3.6 8-8V509.7H624c6.7 0 10.4-7.7 6.3-12.9L518.3 355z"></path>
                        </svg>
                    </i>
                    <i className={ `${ sprefix }-layerMenu-icon` } onClick={ this.handleBtnClick('add') }>
                        <svg viewBox="64 64 896 896" focusable="false" data-icon="arrow-up" width="1em" height="1em" fill="currentColor" aria-hidden="true">
                            <path d="M868 545.5L536.1 163a31.96 31.96 0 00-48.3 0L156 545.5a7.97 7.97 0 006 13.2h81c4.6 0 9-2 12.1-5.5L474 300.9V864c0 4.4 3.6 8 8 8h60c4.4 0 8-3.6 8-8V300.9l218.9 252.3c3 3.5 7.4 5.5 12.1 5.5h81c6.8 0 10.5-8 6-13.2z"></path>
                        </svg>
                    </i>
                    <i className={ `${ sprefix }-layerMenu-icon` } onClick={ this.handleBtnClick('reduce') }>
                        <svg viewBox="64 64 896 896" focusable="false" data-icon="arrow-down" width="1em" height="1em" fill="currentColor" aria-hidden="true">
                            <path d="M862 465.3h-81c-4.6 0-9 2-12.1 5.5L550 723.1V160c0-4.4-3.6-8-8-8h-60c-4.4 0-8 3.6-8 8v563.1L255.1 470.8c-3-3.5-7.4-5.5-12.1-5.5h-81c-6.8 0-10.5 8.1-6 13.2L487.9 861a31.96 31.96 0 0048.3 0L868 478.5c4.5-5.2.8-13.2-6-13.2z"></path>
                        </svg>
                    </i>
                    <i className={ `${ sprefix }-layerMenu-icon` } onClick={ this.handleBtnClick('last') }>
                        <svg viewBox="64 64 896 896" focusable="false" data-icon="vertical-align-bottom" width="1em" height="1em" fill="currentColor" aria-hidden="true">
                            <path d="M859.9 780H164.1c-4.5 0-8.1 3.6-8.1 8v60c0 4.4 3.6 8 8.1 8h695.8c4.5 0 8.1-3.6 8.1-8v-60c0-4.4-3.6-8-8.1-8zM505.7 669a8 8 0 0012.6 0l112-141.7c4.1-5.2.4-12.9-6.3-12.9h-74.1V176c0-4.4-3.6-8-8-8h-60c-4.4 0-8 3.6-8 8v338.3H400c-6.7 0-10.4 7.7-6.3 12.9l112 141.8z"></path>
                        </svg>
                    </i>
                </div>
                <div className={ `${ sprefix }-layerMenu-center` }>
                        <ul className={ `${ sprefix }-layerMenu-menu ${ sprefix }-layerMenu-menu-inline ${ sprefix }-layerMenu-menu-dark` }>
                            { this.renderPanel(data, {}) }
                        </ul>
                </div>
                <div className={ `${ sprefix }-layerMenu-footer` }></div>
            </div>
        );
    }
}



