import React, { Component } from 'react';
import PropTypes from "prop-types";
import './ComponentMenu.less';
export default class ComponentMenu extends Component {
    static propTypes = {
        sprefix: PropTypes.string
    };
    static defaultProps = {
        sprefix: 'dw'
    };
    constructor(props) {
        super(props);
        let data = props.defaultData;
        this.hotKey = null;
        if ('data' in props) {
            data = props.data || [];
        }
        let activeKey = props.defaultActiveKey || [];
        if ('activeKey' in props) {
            activeKey = props.activeKey || [];
        }
        this.state = {
            activeKey,
            data
        };
    }
    componentWillReceiveProps(nextProps) {
        const newState = {};
        if('data' in nextProps) {
            newState.data = nextProps.data || [];
            
        }
        if('activeKey' in nextProps) {
            newState.activeKey = nextProps.activeKey || [];
        }
        this.setState(newState);
    }
    componentDidMount() {
        
    }
    componentWillUnmount(){
        
    }
    render() {
        const { sprefix } = this.props;
        const { data } = this.state;
        return (
            <div className={ `${ sprefix }-componentMenu` }>
                <div className={ `${ sprefix }-componentMenu-content` }>
                    <div className={ `${ sprefix }-componentMenu-inner` }>
                        <div className={ `${ sprefix }-componentMenu-nav` }>
                            组件1
                        </div>
                        <div className={ `${ sprefix }-componentMenu-list` }>
                            df1
                        </div>
                    </div>
                </div>
                <div className={ `${ sprefix }-componentMenu-content` }>
                    <div className={ `${ sprefix }-componentMenu-inner` }>
                        <div className={ `${ sprefix }-componentMenu-nav` }>
                            组件2
                        </div>
                        <div className={ `${ sprefix }-componentMenu-list` }>
                            df2
                        </div>
                    </div>
                </div>
                <div className={ `${ sprefix }-componentMenu-content` }>
                    <div className={ `${ sprefix }-componentMenu-inner` }>
                        <div className={ `${ sprefix }-componentMenu-nav` }>
                            组件3
                        </div>
                        <div className={ `${ sprefix }-componentMenu-list` }>
                            df3
                        </div>
                    </div>
                </div>
                <div className={ `${ sprefix }-componentMenu-content` }>
                    <div className={ `${ sprefix }-componentMenu-inner` }>
                        <div className={ `${ sprefix }-componentMenu-nav` }>
                            组件3
                        </div>
                        <div className={ `${ sprefix }-componentMenu-list` }>
                            df3
                        </div>
                    </div>
                </div>
                <div className={ `${ sprefix }-componentMenu-content` }>
                    <div className={ `${ sprefix }-componentMenu-inner` }>
                        <div className={ `${ sprefix }-componentMenu-nav` }>
                            组件3
                        </div>
                        <div className={ `${ sprefix }-componentMenu-list` }>
                            df3
                        </div>
                    </div>
                </div>
                <div className={ `${ sprefix }-componentMenu-content` }>
                    <div className={ `${ sprefix }-componentMenu-inner` }>
                        <div className={ `${ sprefix }-componentMenu-nav` }>
                            组件3
                        </div>
                        <div className={ `${ sprefix }-componentMenu-list` }>
                            df3
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}



